.button-27 {
    appearance: none;
    background-color: #000000;
    border: 2px solid #1A1A1A;
    border-radius: 15px;
    box-sizing: border-box;
    color: #FFFFFF;
    cursor: pointer;
    display: inline-block;
    font-family: Roobert, -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-size: 16px;
    font-weight: 600;
    line-height: normal;
    margin: 0;
    min-height: 60px;
    min-width: 0;
    outline: none;
    padding: 16px 24px;
    text-align: center;
    text-decoration: none;
    transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
    user-select: none;
    -webkit-user-select: none;
    -webkit-tap-highlight-color: transparent;
    touch-action: manipulation;
    width: 100%;
    will-change: transform;
}

.button-27:disabled {
    pointer-events: none;
}

.button-27:active {
    box-shadow: none;
    transform: translateY(1rem);
}

body {
    background-color: rgb(63, 63, 63);
}

.user-input {
    width: 100%;
    resize: none;
}

.segment-name {
    text-align: center;
    color: cornsilk;
    font-size: 4rem;
    text-decoration: underline;
    font-weight: bold;
    font-family: 'Courier New', Courier, monospace;
    margin-top: 2rem;
}

/* .segment-prompts {
    display: block;
} */

.prompt {
    background-color: #fbe0c2;
    border-radius: 1rem;
    color: rgb(128, 98, 0);
    cursor: pointer;
    display: inline-block;
    padding: 0.8rem 0.8rem;
    border: 0;
    font-size: 0.8rem;
    margin-top: 0.3rem;
    margin-bottom: 0.3rem;
    margin-right: 0.3rem;
    margin-left: 0.3rem;
    /* width: 50%;
    height:auto; */
    /* width: 10rem;
    height: 10rem; */
    overflow: auto;
}

.prompt-title {
    font-size: 1rem;
    font-weight: bold;
    margin-bottom: 0.2rem;
}

.prompt-prompt {
    margin-bottom: 0.2rem;
}

/* .prompt-changes {
    color: rgba(0, 0, 0, .5);
} */

.prompt-enabled {
    background-color: #c2fbd7;
    box-shadow: rgba(44, 187, 99, .2) 0 -25px 18px -14px inset, rgba(44, 187, 99, .15) 0 1px 2px, rgba(44, 187, 99, .15) 0 2px 4px, rgba(44, 187, 99, .15) 0 4px 8px, rgba(44, 187, 99, .15) 0 8px 16px, rgba(44, 187, 99, .15) 0 16px 32px;
    color: green;
}

.prompt-notes {
    font-style: italic;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.prompt-output {
    color: cornsilk;
    margin-top: 2rem;
}

.segment-scroller {
    position: fixed;
    left: 0;
    bottom: 5rem;
    width: 8rem;
}

.segment-scroller-button {
    /* align right for future reference */
    /* display: block;
    margin-left: auto;
    margin-right: 0; */
    font-size: 1rem;
    font-weight: bold;
    margin: 0.2rem; 
    margin-bottom: 0;
    margin-top: 0.5rem;
    height: 2rem;
    background-color: rgba(0, 0, 0, 0.2);
    border: none;
    box-sizing: border-box;
    width: 100%;
    /* color: black; */
    -webkit-text-fill-color: white;
    -webkit-text-stroke-width: 0.02rem;
    -webkit-text-stroke-color: black;
}

.sticky-footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.copy-button {
    margin-bottom: 0.5rem;
    padding: 0.8rem 0.8rem;
    font-size: 1.5rem;
    text-align: center;
    width: 80%;
    height: 4rem;
}

.phantom {
    height: 25rem;
}